.table {
  // Cells
  > thead,
  > tbody,
  > tfoot {
    > tr {
      > th,
      > td {
        padding: 3px;
        border-top: 1px solid #cdd2db;//@table-border-color;
      }
    }
  }
  // Bottom align for column headings
  > thead > tr > th {
    border-bottom: 2px solid #0A6E9D;//#144156;//@table-border-color;
  }
}






//Zebrado
.table-striped {
  > tbody > tr:nth-child(odd) {
    background-color: rgba(223, 226, 231, 0.5);//rgba(223, 226, 231, 0.7);//#dfe2e7;//@table-bg-accent;
    
  }
}

.table-hover {
  > tbody > tr:hover {
    background-color: #C0D4F7;//#A4B9DE;//#66CCFF;//@table-bg-hover;
  }
}

