/*Classe para no quebrar a linha da tabela, oculta somente o que no cabe na coluna, adicionando ... no final*/
.table-ellipsis {
	table-layout:fixed;
}
.table-ellipsis td {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}